import { Session } from '../../../models'

export default function (sessionType) {
  return {
    session: {
      type: sessionType,
      resolve: () => {
        return Session.findOne({'instructor': 'ramesh'}, (err, session) => {
          if (err) throw err
          return {
            time: session.time,
            instructor: session.instructor
          }
        })
      }
    }
  }
}



