export default function (mongoose) {
  const Schema = mongoose.Schema
  const InstitutionSchema = new Schema({
    name: String,
    contact: [{
      email: String,
      phone: String,
      social_links: [String]
    }],
    location: String
  })
  return mongoose.model('institution', InstitutionSchema)
}




